


  .hero-section {
    height: 100vh;
    background-size: cover;
    background-position: center center;
    background-repeat: no-repeat;
    background-attachment: fixed;
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;
    padding-top: 2rem;
}

.hero-content {
    width: 100%;
    max-width: 1200px;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    color: white;
    text-align: center;
    margin-top: 2rem;
    flex-grow: 1;
   
}

.words {
    width: 63%;
    padding: 2rem;
    border-radius: 10px;
   

}

.hero-content h1 {
    font-size: clamp(2rem, 5vw, 3.5rem);
    font-weight: bold;
    line-height: 1.2;
    margin-bottom: 1rem;
    text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.5);
}

.hero-content p {
    font-size: clamp(1rem, 2.5vw, 1.4rem);
    line-height: 1.6;
    margin-bottom: 2rem;
    text-shadow: 1px 1px 2px rgba(0, 0, 0, 0.5);
}

.hero-buttons {
    display: flex;
    gap: 1.5rem;
    justify-content: center;
    flex-wrap: wrap;
}

.btn1 {
    padding: 12px 30px;
    border: none;
    border-radius: 8px;
    cursor: pointer;
    background: #5f117c;
    color: white;
    font-size: clamp(0.9rem, 2vw, 1.1rem);
    font-weight: 600;
    transition: all 0.3s ease;
    min-width: 160px;
}

.btn2 {
    padding: 12px 30px;
    border: 2px solid white;
    border-radius: 8px;
    cursor: pointer;
    background: rgba(255, 255, 255, 0.2);
    color: white;
    font-size: clamp(0.9rem, 2vw, 1.1rem);
    font-weight: 600;
    transition: all 0.3s ease;
    min-width: 160px;
}

.btn1:hover {
    background: #7d1aa5;
    transform: translateY(-2px);
}

.btn2:hover {
    background: rgba(255, 255, 255, 0.3);
    transform: translateY(-2px);
}

.page-content {
    min-height: 90vh;
    padding: 2rem;
 
}

/* Ensure background image covers properly on all devices */
@media (max-width: 1024px) {
    .hero-section {
        background-size: cover;
        background-position: center center;
        height: 80vh;
        padding-top: 1.5rem;
    }
    
    .words {
        width: 75%;
        padding: 1.5rem;
    }
    
    .hero-content {
        margin-top: 1.5rem;
    }
}

@media (max-width: 768px) {
    .hero-section {
        height: 70vh;
        padding-top: 1rem;
        background-attachment: scroll; /* Better performance on mobile */
    }
    
    .words {
        width: 85%;
        padding: 1.2rem;
    }
    
    .hero-content {
        margin-top: 1rem;
    }
}

@media (max-width: 640px) {
    .hero-section {
        height: 60vh;
        padding-top: 0.5rem;
        background-size: cover;
        background-position: center center;
    }
    
    .words {
        width: 90%;
        padding: 1rem;
    }
}

@media (max-width: 480px) {
    .hero-section {
        height: 50vh;
        background-size: cover;
        background-position: center center;
    }
    
    .words {
        width: 95%;
        padding: 0.8rem;
    }
}